<template>
  <div class="feedback-history">
    <app-header title="反馈历史"></app-header>

    <section class="feedback-history-main">
      <empty-page
        :show-empty="!resList.length"
        :empty-msg="emptyMsg"
        :empty-img-src="emptyImgSrc"
      />
      <van-list
        style
        v-if="resList.length"
        v-model="loading"
        :finished="finished"
        finished-text="— 干工程是有底线的 —"
        @load="onLoad"
      >
        <van-cell class="fh-item" v-for="item in list" :key="item.id">
          <div class="feedback-history-item">
            <div class="feedback-history-time">{{ item.feedback_on }}</div>
            <div class="feedback-history-text">
              <p :style="{ '-webkit-line-clamp': 0 }">
                {{ item.content }}
              </p>
            </div>
          </div>
        </van-cell>
      </van-list>
      <!-- <div class="feedback-history-content" v-if="!showEmpty">
        <div class="feedback-history-item">
          <div class="feedback-history-time">2019-10-22 21:12:12</div>
          <div class="feedback-history-text">
            <p>干工程是有底线的干ssss干ssdsfssfs工程是有底线的干工程是有底线的干工程是有底线的干工程是有底线的干工程是有底线的</p>
