      </div>-->
    </section>
  </div>
</template>
<script>
import { getFeedbackList } from '@src/api/feedback';
import { chunk } from 'underscore';
export default {
  data() {
    return {
      list: [{}],
      resList: [],
      loading: false,
      finished: false,
      showEmpty: false,
      emptyMsg: '暂无反馈历史',
      emptyImgSrc: require('@assets/images/common/icon-empty.png'),
      pullLoading: false,
    };
  },
  activated() {
    this.finished = false;
    this.getList();
  },
  methods: {
    // 上滑加载
    onLoad() {
      if (this.resList.length >= 0) {
        setTimeout(() => {
          // 将数组每7个分成一组
          const chunkList = chunk(this.resList, 7);
          // 当前列表数
          const mLength = this.list.length;
          // 当前组数 + 1
          let n = mLength >= 7 ? (mLength % 7) + 1 : 1;
          console.log('chunkList,mLength,n', chunkList, mLength, n);
          // 将下一组的数据合并到当前组
          this.list = [...this.list, ...(chunkList[n] || [])];
          console.log('this.list', this.list);
          // 加载状态结束
          this.loading = false;

          // 数据全部加载完成
          if (this.list.length >= this.resList.length) {
            this.finished = true;
          }
        }, 500);
      }
    },
    // 获取列表
    getList() {
      getFeedbackList()
        .then(res => {
          this.resList = res;
          this.list = chunk(res, 7)[0] || [];
          console.log(this.list, '0000000000');
          this.isLoading = false;
          this.loading = false;
          this.finished = false;
        })
        .catch(error => {
          console.log('获取反馈列表失败', error);

          this.isLoading = false;
          this.loading = false;
          this.finished = false;
        });
    },
  },
};
</script>
